*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Courier New', Courier, monospace;
}
.main{
    height: 100vh;
    width: 100%;
    background:lightcyan;
    display: flex;
    align-items: center;
    justify-content: center;
}
.box{
    height: 65vh;
    width: 18rem;
    background:burlywood;
    border: 0.3rem solid black;
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.content{
 /*   background: chartreuse; */
    height: 15rem;
    width: 13rem;
    display: flex;
    flex-direction: column;
    align-items: center;    
    justify-content: space-evenly;
}
.center{
    display: flex;
}
.footer{
 /*   background: crimson; */
    height:10rem;
    width: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.min-max{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.line{
    font-size: 3rem;
}
.search_box{
    height: 2.7rem;
    border-radius: 2rem;
}
.city_name{
    font-size: 3rem;
    text-transform: capitalize;
}
.city_temp{
    font-size: 2rem;
}
